import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import $ from 'jquery';

function Header() {
 useEffect(()=>{
  $('.dropdown_title li').on({
    'mouseover':function(){
      $('.dropdown_sub').css('display','block').stop().slideDown(800);
    }
  });
  $('.dropdown_sub').on({
    'mouseleave':function(){
      $('.dropdown_sub').css('display','block').stop().slideUp(800);
    }
  });
 });
  return (
    <header id="header">
      <div className="header_inner">
        <h2 className="logo_text">
          {/* Link to browser router : process.env.PUBLIC_URL */}
          <Link to={"/ko"}>
            NAVER <span>whale</span>
          </Link>
        </h2>
        <div className="inner">
          <ul className="dropdown_title">
            <li>
              <Link to={"/ko/story"}>Surfing My Way</Link>
            </li>
            <li>
              <Link to={"/tab1"}></Link>
            </li>
            <li>
              <Link to={"/tab2"}></Link>
            </li>
            <li>
              <Link to={"/tab3"}></Link>
            </li>
            <li>
              <Link to={"/tab4"}> </Link>
            </li>
            <li>
              <Link to={"/tab5"}> </Link>
            </li>
          </ul>
        </div>
        <button className="loginForm">
          <Link to={"/Login"}></Link>
        </button>
        <button className="signForm">
          <Link to={"/SignUp"}></Link>
        </button>
      </div>
      <div className="dropdown_sub">
        <div className="inner">
          <div className="sub_wrap">
            <ul>
              <li><Link to={"/ko/story/"}>Brand Story</Link></li>
              <li><Link to={"/ko/details/omnitasking"}> </Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}>What's NEW</Link></li>
              <li><Link to={"/sub_tab"}> </Link></li>
              <li><Link to={"/sub_tab"}> </Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}></Link></li>
              <li><Link to={"/sub_tab"}></Link></li>
              <li><Link to={"/sub_tab"}></Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}> </Link></li>
              <li><Link to={"/sub_tab"}></Link></li>
              <li><Link to={"/sub_tab"}></Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}> </Link></li>
              <li><Link to={"/sub_tab"}></Link></li>
              <li><Link to={"/sub_tab"}> </Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}> </Link></li>
              <li><Link to={"/sub_tab"}> OS</Link></li>
              <li><Link to={"/sub_tab"}> </Link></li>
            </ul>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
