import React from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import {Link} from "react-router-dom";

export default function SignUp() {
  return (
    <>
      <Form>
        <Form.Group className="mb-3 id" controlId="formBasicId">
          <Form.Label></Form.Label>
          <Form.Control type="ID" placeholder="Enter ID" />
          <Form.Text className="text-muted"> .</Form.Text>
        </Form.Group>

        <Form.Group className="mb-3 nick" controlId="formBasicNick">
          <Form.Label></Form.Label>
          <Form.Control type="ID" placeholder="Enter Nickname" />
          <Form.Text className="text-muted"> .</Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label></Form.Label>
          <Form.Control type="password" placeholder="Enter password" />
          <Form.Text className="text-muted">ex) /,, </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label> </Form.Label>
          <Form.Control type="password" placeholder="Enter password" />
          <Form.Text className="text-muted">     </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label></Form.Label>
          <Form.Control type="email" placeholder="Enter email address" />
          <Form.Text className="text-muted">ex) abc123@gmail.com</Form.Text>
        </Form.Group>

        <Form.Group className="mb-4" controlId="formBasicCheckbox"></Form.Group>
        <Button variant="primary" type="submit" className="SignBtn">
          
        </Button>
        <Button variant="primary" type="text" className="cancel">
          <Link to={"/ko"}></Link>
        </Button>
      </Form>
    </>
  );
}
