import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { BsCheck, BsChevronDoubleDown } from "react-icons/bs";

export default function Custom() {
  let [h2_item, h2_change] = useState([
    "",
    " & ",
    "",
  ]);

  let [p_item, p_change] = useState([
    "    !",
    "   !",
    "     !",
  ]);

  let [span_item, span_change] = useState([" "]);

  return (
    <>
      {/**/}
      <section id="source_container">
        <div className="tabs_03 main">
          <div className="inner">
            <div className="main_text">
              <h2>
                  ,
                <br />
                 !
              </h2>
              <p>  ,   !</p>
              <button>
                 <BsChevronDoubleDown />
              </button>
            </div>
          </div>
        </div>

        {/* item_01 */}
        <div className="tabs_03 item_01">
          <div className="inner">
            <div className="item_text">
              <h2>
                <img
                  src={require("../../images/sub_skill/custom/icon_why_tab3_01.png")}
                  alt=" "
                />
                {h2_item[0]}
              </h2>
              <p>{p_item[0]}</p>
              <Link to="/desktop">
                <span>
                  <img
                    src={require("../../images/sub_skill/icon_detail_arrow.png")}
                    alt=" "
                  />
                  {span_item[0]}
                </span>
              </Link>
              <div>
                <img
                  src={require("../../images/sub_skill/icon_pc.png")}
                  alt=" "
                />
                <img
                  src={require("../../images/sub_skill/icon_m.png")}
                  alt=" "
                />
              </div>
            </div>
            <div className="item_play">
              {/*  gif */}
              <img
                src={require("../../images/main/sw1_gif1.gif")}
                alt=" "
              />
            </div>
          </div>
        </div>

        {/* item_02 */}
        <div className="tabs_03 item_02">
          <div className="inner">
            <div className="item_text">
              <h2>
                <img
                  src={require("../../images/sub_skill/custom/icon_why_tab3_02.png")}
                  alt=" "
                />
                {h2_item[1]}
              </h2>
              <p>{p_item[1]}</p>
              <Link to="/desktop">
                <span>
                  <img
                    src={require("../../images/sub_skill/icon_detail_arrow.png")}
                    alt=" "
                  />
                  {span_item[0]}
                </span>
              </Link>
              <div>
                <img
                  src={require("../../images/sub_skill/icon_pc.png")}
                  alt=" "
                />
                <img
                  src={require("../../images/sub_skill/icon_m.png")}
                  alt=" "
                />
              </div>
            </div>
            <div className="item_play">
              {/*  gif */}
              <img
                src={require("../../images/main/sw1_gif1.gif")}
                alt=" "
              />
            </div>
          </div>
        </div>

        {/* item_03 */}
        <div className="tabs_03 item_03">
          <div className="inner">
            <div className="item_text">
              <h2>
                <img
                  src={require("../../images/sub_skill/custom/icon_why_tab3_03.png")}
                  alt=" "
                />
                {h2_item[2]}
              </h2>
              <p>{p_item[2]}</p>
              <Link to="/desktop">
                <span>
                  <img
                    src={require("../../images/sub_skill/icon_detail_arrow.png")}
                    alt=" "
                  />
                  {span_item[0]}
                </span>
              </Link>
              <br />
              <div>
                <img
                  src={require("../../images/sub_skill/icon_pc.png")}
                  alt=" "
                />
              </div>
            </div>
            <div className="item_play">
              {/*  gif */}
              <img
                src={require("../../images/main/sw1_gif1.gif")}
                alt=" "
              />
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
