import React from "react";
import {Link} from "react-router-dom";

function Story() {
  return (
    <>
      <section id="brandStory">
        <div className="inner">
          <div className="button_whale">
            <img src={require('../images/scrollWhale.png')} alt="" />
          </div>
          <figcaption>
            <h4>Brand Story</h4>
            <h2> 1  </h2>
          </figcaption>
          <figure>
            <img
              src={require("../images/sub_brandStroy/notebook.png")}
              alt=""
            />
            <div className="video">
              <video
                src={require("../images/sub_brandStroy/cloud.mp4")}
                autoPlay
                muted
                loop
              ></video>
            </div>
          </figure>
        </div>
        <p>    ,  .</p>
      </section>

      <section id="sns_wrap">
        <h3>
          <img
            src={require("../images/sub_brandStroy/brand_icon.png")}
            alt="  "
          />
          <p>
              <span>   </span>
          </p>
          <p>  SNS  !</p>
        </h3>
        <ul>
          <li><Link to={"/blog"}><img src={require('../images/sub_brandStroy/sns_blog.png')} alt="" /></Link></li>
          <li><Link to={"/tv"}><img src={require('../images/sub_brandStroy/sns_tv.png')} alt="" /></Link></li>
          <li><Link to={"/insta"}><img src={require('../images/sub_brandStroy/sns_insta.png')} alt="" /></Link></li>
          <li><Link to={"/facebook"}><img src={require('../images/sub_brandStroy/sns_facebook.png')} alt="" /></Link></li>
          <li><Link to={"/youtube"}><img src={require('../images/sub_brandStroy/sns_youtube.png')} alt="" /></Link></li>
          <li><Link to={"/twitter"}><img src={require('../images/sub_brandStroy/sns_twitter.png')} alt="" /></Link></li>
        </ul>
      </section>
    </>
  );
}

export default Story;
